.slide {
  display: flex;
  justify-content: center;
  .container {
    width: 1400px;
    .carousel-prev,.carousel-next {
      width: 50px;
      height: 50px;
      background-color: rgb(243, 243, 243);
      border-radius: 50%;
      position: absolute;
      top: 0;
      bottom:0;
      margin: auto;
    }
  }
}